/* sort file by size*/

.sort-files-button a {
    color: white;
    text-decoration: none;
}


.container-sort {
    display: flex;
  }
  
  .left-column {
    flex: 1;
    padding-left: 10%;
    width: 30%;
  }
  
  .right-column {
    padding: 10%;
    flex: 0 0 auto;
    width: 70%;
    display: flex;
    flex-direction: column;
  }

  .sort-files-button {
    margin-bottom: 5%;
    width: 10vw;
    display: flex;
    background-color:#8c8c8c;
	border-radius:28px;
	border:1px solid #525252;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-size:1.7rem;
	padding:16px 31px;
	text-decoration:none;
  }



/* typing challenge */
.form {
    margin-left: 10vw;
    margin-right: 10vw;
}

.typing-challenge-text {
    width: 100%;
    padding: 0.4rem;
    margin-top: 1vw;
    margin-bottom: 1vw;
    text-align: center;
    font-size: 1.5rem;
    border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  box-shadow: 6px 6px 12px #c5c5c5,
              -6px -6px 12px #ffffff;
}

#typing-challenge-input {
    width: 100%;
    font-size: 1.5rem;
    padding: 0.4rem;
    vertical-align: middle;
    box-shadow: 0 0 0.5em rgb(71, 71, 71);
}

#typing-challenge-input.shake {
    animation: shake 0.2s ease-in-out 0s 2;
    box-shadow: 0 0 0.5em red;
}

@keyframes shake {
    0% { margin-left: 0rem; }
    25% { margin-left: 0.5rem; }
    75% { margin-left: -0.5rem; }
    100% { margin-left: 0rem; }
}

/*  how to connect  */
.how-to-connect-column {
    float: left;
    width: 20%;
    padding: 10px;
  }

  li {
    list-style-type: none;
  }

.how-to-connect-container{
    margin-left: 15%;
}


.set-path-dots{
position: absolute;
bottom: 2vh;
text-align: center;
padding: 2%;
}

.set-path-dot, .set-path-dot-check{
    display: inline-block;
  height: 5vh;
  width: 8vw;
  border-radius: 0.5rem;
  cursor: pointer;
  margin: 2%;
  padding: 2%;
  background-color: #686868;
  color: #e4e4e4;
}

.set-path-add-remove{
   display: flex;
   flex-direction: row;
}
.set-path-dots{
    margin-left: 10%;
}

/* input boxes - create packet quizz */
.create-packets-input-group input{
    font-size: 1rem;
    border: none;
    border-radius: 1rem;
    background: #e8e8e8;
    box-shadow: 3px 3px 2px #c5c5c5,
        -10px -10px 180px #ffffff;
    transition: 0.3s;
    margin: 2%;
    margin-left: 2.5vw;
}
  

.create-packets-input-group input:focus{
    outline-color: #e8e8e8;
    background: #e8e8e8;
    box-shadow: inset 20px 20px 60px #c5c5c5,
        inset -20px -20px 60px #ffffff;
    transition: 0.3s;
}
  
