/* Plus/Minus Zoom Button Styles changing flow */
.zoom-buttons-container{
  position: absolute;
  bottom: 4vh;
  z-index: 1;
  right: 2vw ;
  height: 10vh;
  width: 2.5vw;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  box-shadow: 3px 3px 6px #c5c5c5,
              -6px -6px 12px #ffffff;
}

.zoom-in, .zoom-out {
  border-radius: 0.5em;
  height: 5vh;
  cursor: pointer;
  width: 2.5vw;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;

}
.zoom-in {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.zoom-out {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  top: 5vh;
}

.zoom-out::before {
  content: "";
  display: block;
  width: 80%;
  border-bottom: 1px solid #696666;
  position: absolute;
  top: 5vh;
  left: 10%;
}

.zoom-button img {
  width: 0.8vw;
  height: auto;
  transition: transform 0.2s ease-out;
}

.zoom-button:hover img{
  transform: scale(1.3);
}

/* close/open window - template */

.close-open-content {
  width: 20vw;
  height: 60vh;
  position: absolute;
  bottom: 7vh;
  right: 3vw;
  z-index: 15;
  border-radius: 0.5em;
  background: #c7c7c7;
  border: 1px solid #bebebe;
  box-shadow: 6px 6px 12px #7c7c7c,
  -6px -6px 12px #ffffff;
}

/* pulsing button */ 
.pulse-button {
  border: none;
  box-shadow: 0 0 0 0 rgba(122, 122, 122, 0.7);
  border-radius: 50%;
  background-color: #2c2c2c;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
  animation: pulse 2s infinite cubic-bezier(0.4, 0, 0.6, 1);
}

.pulse-button:hover {
  animation: none;
  transform: scale(1.2);
}

@keyframes pulse {
  to {
    box-shadow: 0 0 0 45px rgba(232, 76, 61, 0);
    transform: scale(1.2);
  }
}

/* minimize/maximize pulsing button openclose window  */

.close-open-controls button {
  background-color: rgb(49, 49, 49);
  color: rgb(237, 237, 237);
  border: none;
  padding: 15px 15px;
  border-radius: 50px;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  width: 50px;
  position: absolute;
  bottom: 3vh;
  right: 1vw;
  z-index: 15;
}

/* check-validity-button close open window */

.check-button.close-open-window {
  position: absolute;
  bottom: 4%;
  right: 27%;
  background-color: #fff;
  border-radius: 2rem;
  border-style: none;
  color: #3c4043;
  cursor: pointer;
  font-family: "Google Sans",Roboto,Arial,sans-serif;
  font-size: 14px;
  height: 7vh;
  padding: 2px 24px;
  
}

.check-button.close-open-window:hover {
  background: #F6F9FE;
  color: #1d1d1d;
}

.check-button.close-open-window:active {
  box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%), 0 8px 12px 6px rgb(60 64 67 / 15%);
  outline: none;
}


/*  create packet  */
.create-packets-form-container {
  width: 10vw;
  margin-left: 1.3vw;
}

.create-packets-form-container .create-packets-input-group {
  margin: 2vh;
}

/* input boxes - create packet quizz */
.create-packets-input-group input{
  font-size: 1rem;
  border: none;
  border-radius: 1rem;
  background: #e8e8e8;
  box-shadow: 3px 3px 2px #c5c5c5,
      -10px -10px 180px #ffffff;
  transition: 0.3s;
}

.create-packets-input-group input[type=text] {
  padding: 10px 20px;
}

.create-packets-input-group input:focus{
  outline-color: #e8e8e8;
  background: #e8e8e8;
  box-shadow: inset 20px 20px 60px #c5c5c5,
      inset -20px -20px 60px #ffffff;
  transition: 0.3s;
}

/* message to send */
.message-into-create-packet{
  margin-left: 2%;
  margin-right: 2%;
}

/* Slideshow */

.create-packets-slideshow {
  margin: 0 auto;
  overflow: hidden;
}

.create-packets-slideshowSlider {
  white-space: nowrap;
  transition: ease 500ms;
  margin-bottom: 20px;
}

.create-packets-slide {
  display: inline-block;
  height: 200px;
  width: 300px;
  border-radius: 40px;
}

/* Buttons of slides show*/

.create-packets-slideshowDots {
  text-align: center;
}

.create-packets-slideshowDot {
  display: inline-block;
  height: 4vh;
  width: 4vw;
  border-radius: 0.5rem;
  cursor: pointer;
  margin: 5%;
  padding: 1%;
  background-color: #686868;
  color: #e4e4e4;
}

.create-packets-slideshowDot.active {
  color: #c5c5c5;
  background-color: #292929;
}

